require('./bootstrap');

$(function(){
    let countryDatatable = $('#paises').DataTable({
        orderCellsTop: true,
        fixedHeader: true,
        processing: true,
        serverSide: false,
        ajax: '',
        columns: [
            { data: 'name', name: 'name', targets: 0, visible: true },
            { data: 'code', name: 'code', targets: 1, visible: true },
        ],
        language: {
            sEmptyTable: 'Nenhum registro encontrado',
            sInfo: 'Mostrando de _START_ até _END_ de _TOTAL_ registros',
            sInfoEmpty: 'Mostrando 0 até 0 de 0 registros',
            sInfoFiltered: '(Filtrados de _MAX_ registros)',
            sInfoPostFix: '',
            sInfoThousands: '.',
            sLengthMenu: 'Mostrar _MENU_ registros',
            sLoadingRecords: 'Carregando...',
            sProcessing: 'Processando...',
            sZeroRecords: 'Nenhum registro encontrado',
            sSearch: 'Pesquisar',
            oPaginate: {
                sNext: 'Próximo',
                sPrevious: 'Anterior',
                sFirst: 'Primeiro',
                sLast: 'Último',
            },
            oAria: {
                sSortAscending: ': Ordenar colunas de forma ascendente',
                sSortDescending: ': Ordenar colunas de forma descendente',
            },
        },
    });

    $('#btn-list-countries').on('click', function (){
        let url = $('meta[name="country-getAll"]').attr('content');
        countryDatatable.ajax.url(url).load();

    });

    $('#btn-download-countries-csv').on('click', function (){
        let url = $('meta[name="country-exportCSV"]').attr('content');
        location.href = url;
    });


});
